import Vue from 'vue'
import Router from 'vue-router'
import Mapa from './views/Mapa.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Mapa
    },
    {
      path: '/dodaj',
      name: 'dodaj',
      component: () => import('./views/Dodaj.vue')
    },
    {
      path: '/prijava',
      name: 'prijava',
      component: () => import('./views/Prijava.vue')
    }
  ]
})
